
import { defineComponent, reactive, toRefs } from "vue";

export default defineComponent({
  setup() {
    const state = reactive({
      missionName: "",
    });

    return { ...toRefs(state) };
  },
  methods: {
    addMission() {
      this.$emit("addMissionEmit", this.missionName);
      this.missionName = "";
    },
  },
});
