
import { defineComponent, reactive, toRefs } from "vue";
const pages = ["todolist", "analytics", "ringtones"];

export default defineComponent({
  name: "MenuGroup",
  props: {
    advancedMode: {
      type: Boolean,
      default: false,
    },
    currentPage: {
      type: String || null,
      default: null,
    },
  },
  setup(props, { emit }) {
    // @data
    const state = reactive({
      pages: pages,
    });

    // @method
    function itemClick(page: string) {
      emit("menuItemClickEmit", page);
    }

    return {
      ...toRefs(state),
      itemClick,
    };
  },
});
